import React from "react";
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';

import ArrowLeftIcon from '@material-ui/icons/ArrowLeft';
import ArrowRightIcon from '@material-ui/icons/ArrowRight';

const PaginationButtons = (props) => {
    const currentPage = 5
    const numOfPage = 7
    const arrayLength = numOfPage - 2

    return (
        <ButtonGroup size="small" aria-label="small contained button group">
            <Button color={currentPage === 1 ? "secondary": ""} disabled={currentPage === 1}>
                <ArrowLeftIcon />
            </Button>
            <Button disabled>...</Button>
            {Array.from({ length: arrayLength }).map((i, index) => (
                <Button key={index} color={currentPage === (index+2) ? "secondary": ""}>{index+2}</Button>
            ))}
            <Button disabled>...</Button>
            <Button color={currentPage === numOfPage ? "secondary": ""} disabled={currentPage === numOfPage}>
                <ArrowRightIcon />
            </Button>
        </ButtonGroup>
    )
}

export default PaginationButtons